h3 {
    margin: 0;
}

.hireBlock {
    background-color: #d1bdb9;
    min-height: 200px;
    min-width: 280px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hireContainer {
    width: 80%;
    height: 80%;
    flex-direction: column;
    justify-content: space-evenly;

    border: 1px solid;
}

.hireTitle {
    background-color: #d683f1;
}

.hireButton {
    margin-top: 20px;
    border-radius: 15px;
    min-height: 35px;
    min-width: 95px;
}